.app-container {
  background: none;
  flex-direction: column;
  align-items: center;
  width: 100vw;
  min-height: 100vh;
  display: flex;
}

.header {
  color: #fff;
  text-align: center;
  background: linear-gradient(90deg, #f7971e 0%, #ffd200 100%);
  border-bottom-right-radius: 40px;
  border-bottom-left-radius: 40px;
  width: 100vw;
  padding: 1.5rem 0 1rem;
  font-family: Comic Sans MS, Comic Sans, cursive, sans-serif;
  box-shadow: 0 4px 16px #00000014;
}

.header h1 {
  letter-spacing: 2px;
  text-shadow: 2px 2px #ffb347, 4px 4px #fc3;
  margin: 0;
  font-size: 2.5rem;
}

.score {
  color: #f7971e;
  background: #fffbe7;
  border-radius: 20px;
  padding: .5rem 1.2rem;
  font-size: 1.2rem;
  font-weight: bold;
  position: absolute;
  top: 1.5rem;
  right: 2rem;
  box-shadow: 0 2px 8px #00000012;
}

.surah-select {
  text-align: center;
  margin-top: 2.5rem;
}

.surah-list {
  flex-wrap: wrap;
  justify-content: center;
  gap: 1.5rem;
  margin-top: 2rem;
  display: flex;
}

.surah-btn {
  color: #fff;
  cursor: pointer;
  background: linear-gradient(135deg, #43cea2 0%, #185a9d 100%);
  border: none;
  border-radius: 30px;
  padding: 1.2rem 2.5rem;
  font-family: Comic Sans MS, Comic Sans, cursive, sans-serif;
  font-size: 1.5rem;
  transition: transform .1s, box-shadow .1s;
  box-shadow: 0 4px 16px #0000001a;
}

.surah-btn:hover {
  transform: scale(1.08)rotate(-2deg);
  box-shadow: 0 8px 24px #43cea22e;
}

.puzzle-section {
  background: #fffbe7;
  border-radius: 30px;
  width: 100vw;
  max-width: 600px;
  min-height: 300px;
  margin-top: 2rem;
  padding: 2rem 1.5rem 2.5rem;
  position: relative;
  box-shadow: 0 4px 24px #00000014;
}

.back-btn {
  color: #fff;
  cursor: pointer;
  background: #ffb347;
  border: none;
  border-radius: 50%;
  width: 2.5rem;
  height: 2.5rem;
  font-size: 1.5rem;
  transition: background .2s;
  position: absolute;
  top: 1.5rem;
  left: 1.5rem;
  box-shadow: 0 2px 8px #00000012;
}

.back-btn:hover {
  background: #ffd200;
}

.puzzle-placeholder {
  text-align: center;
  color: #aaa;
  margin-top: 3rem;
  font-size: 1.2rem;
}

@media (width <= 600px) {
  .header h1 {
    font-size: 1.5rem;
  }

  .score {
    padding: .3rem .8rem;
    font-size: 1rem;
    top: 1rem;
    right: 1rem;
  }

  .puzzle-section {
    padding: 1rem .5rem 1.5rem;
  }

  .surah-btn {
    padding: .8rem 1.2rem;
    font-size: 1.1rem;
  }
}

.order-puzzle {
  text-align: center;
  margin-top: 2rem;
}

.ayat-list {
  flex-direction: column;
  gap: 1rem;
  margin: 2rem 0;
  display: flex;
}

.ayat-card {
  color: #fff;
  cursor: grab;
  user-select: none;
  background: linear-gradient(90deg, #43cea2 0%, #185a9d 100%);
  border-radius: 20px;
  padding: 1rem 1.5rem;
  font-size: 1.2rem;
  transition: background .2s, transform .1s;
  box-shadow: 0 2px 8px #0000001a;
}

.ayat-card:active {
  color: #185a9d;
  background: #ffd200;
}

.check-btn:hover {
  background: #43cea2;
}

@keyframes pop {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes shake {
  0%, 100% {
    transform: translateX(0);
  }

  20%, 60% {
    transform: translateX(-10px);
  }

  40%, 80% {
    transform: translateX(10px);
  }
}

.missing-word-puzzle {
  text-align: center;
  margin-top: 2rem;
}

.ayah {
  color: #185a9d;
  margin: 2rem 0 1.5rem;
  font-size: 1.3rem;
  font-weight: bold;
}

.blank {
  color: #ff4e50;
  margin: 0 .5rem;
  font-size: 1.5rem;
}

.mc-puzzle {
  text-align: center;
  margin-top: 2rem;
}

.question {
  color: #185a9d;
  margin: 2rem 0 1.5rem;
  font-size: 1.2rem;
  font-weight: bold;
}

.options {
  flex-wrap: wrap;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 1.5rem;
  display: flex;
}

.option-btn {
  color: #fff;
  cursor: pointer;
  background: linear-gradient(135deg, #f7971e 0%, #ffd200 100%);
  border: none;
  border-radius: 20px;
  padding: .7rem 2rem;
  font-family: Comic Sans MS, Comic Sans, cursive, sans-serif;
  font-size: 1.1rem;
  transition: background .1s, transform .1s;
  box-shadow: 0 2px 8px #0000001a;
}

.option-btn.selected, .option-btn:hover {
  color: #fffbe7;
  background: #43cea2;
  transform: scale(1.08);
}

.check-btn {
  color: #fff;
  cursor: pointer;
  background: #43cea2;
  border: none;
  border-radius: 20px;
  margin-top: 1rem;
  padding: .7rem 2rem;
  font-family: Comic Sans MS, Comic Sans, cursive, sans-serif;
  font-size: 1.1rem;
  transition: background .2s;
  box-shadow: 0 2px 8px #00000012;
}

.check-btn:disabled {
  color: #aaa;
  cursor: not-allowed;
  background: #eee;
}

.result {
  margin-top: 1rem;
  font-size: 1.3rem;
  font-weight: bold;
}

.result.success {
  color: #43cea2;
  animation: .5s pop;
}

.result.fail {
  color: #ff4e50;
  animation: .5s shake;
}
